<template>
  <div class="search">
    <van-nav-bar
      title="商品搜索"
      left-arrow
      @click-left="$router.go(-1)"
    />

    <van-search
      show-action
      placeholder="请输入搜索关键词"
      clearable
      autofocus
      v-model="searchStr"
    >
      <template #action>
        <div @click="goSearch(searchStr)">搜索</div>
      </template>
    </van-search>

    <!-- 搜索历史 -->
    <div class="search-history">
      <div class="title">
        <span>最近搜索</span>
        <van-icon
          name="delete-o"
          size="16"
          @click="clear"
        />
      </div>
      <div class="list">
        <div
          class="list-item"
          @click="goSearch(item)"
          v-for="item in history"
          :key="item"
        >
          {{ item }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getHistoryList,
  setHistoryList,
  removeHistoryList,
} from '@/utils/storage'
import { Toast } from 'vant'
export default {
  name: 'SearchIndex',
  data() {
    return {
      searchStr: '',
      history: getHistoryList(),
    }
  },
  methods: {
    goSearch(str) {
      if (str.length == 0) {
        Toast('请输入搜索商品')
        return
      }
      const index = this.history.indexOf(str)
      if (index !== -1) {
        this.history.splice(index, 1)
      }
      this.history.unshift(str)
      setHistoryList(this.history)
      this.$router.push(`/searchlist?search=${str}`)
    },
    clear() {
      console.log('清除记录')
      this.history = []
      //removeHistoryList()
      setHistoryList(this.history)
    },
  },
}
</script>

